:root {
  display: flex;
  height: 100vh;
  background-color: #c2c2d6;
  justify-content: center;
  align-items: center;
}

.calculator-container {
  margin: 0 auto;
  display: grid;
  max-width: 300px;
  grid-template-columns: repeat(4, 75px);
  grid-template-rows: 60px repeat(5, 75px);
}


.item {
  color: #fff;
  padding: 0.85em;
  background: rgb(102, 102, 102);
  border: 1px solid black;
}

.item:not(.display) {
  font-size: 1.2em;
}

.item.number,
.item.zero,
.item.dot {
  background-color: #4d4d4d;
}

.display {
  background-color: black;
  grid-column-start: 1;
  grid-column-end: 5;
}

.display p {
  margin-top: -0.5em;
  display: flex;
  justify-content: flex-end;
}

.expression {
  color: yellow;
}

.ac {
  background-color: rgb(172, 57, 57);
  grid-column: 1 / span 2;
}

.zero {
  grid-column: 1 / 3;
}

.equals {
  background-color: rgb(0, 68, 102);
  grid-column-start: 4;
  grid-row: 5 / span 2;
}